.footercontainer {
  display: flex;
  justify-content: space-between;
  width: 90%;
  padding: 2rem;
}
.footerPage {
  width: 100vw;
  background-color: #f9f9f9;
}
.footerleft {
  width: 50%;
  height: 10rem;
}
.footerleft img {
  height: 25%;
  padding-bottom: 1rem;
}
.footerleft p {
  font-size: 0.7rem;
  line-height: 1rem;
}
.footerright {
  width: 50%;
  height: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: end;
}
.footerright p {
  font-size: 0.75rem;
  line-height: 2rem;
  display: flex;
  flex-direction: column;
  text-align: left;
}
