.navbar {
  display: flex;
  justify-content: space-between;
  margin-left: 5rem;
  margin-top: 1rem;
}
.navbar-brand img {
  height: 2.7rem;
}
.navbar-links {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 27rem;
  background-color: #f9f9f9;
  border-radius: 30px;
  margin-right: 6rem;
}
.navbar-links a {
  text-decoration: none;
  color: black;
  font-family: "Nunito", sans-serif;
}
